import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http';
import { Beyond } from '../../../utilities/beyond';
import { Global } from '../../../global/global';

@Component({
  providers: [Beyond],
  selector: 'app-registration-confirmation',
  templateUrl: './registration-confirmation.component.html',
  styleUrls: ['./registration-confirmation.component.scss']
})
export class RegistrationConfirmationComponent implements OnInit {
  prozCode: any;
  proMailId: string;
  mailStatus: boolean = false;
  constructor(
    public beyond: Beyond,
    public http: Http,
    public router: Router,
    private activatedRoute: ActivatedRoute,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.prozCode = this.activatedRoute.snapshot.params['prozId']
    this.getProInfo(this.prozCode)
    //console.log("prozcode"+this.prozCode)
  }
  getProInfo(id) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getProMail/' + id, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status = 200) {
          this.proMailId = response.response[0].emailId;
          this.mailStatus = true
        } else {
          this.mailStatus = false
        }


      }, error => {

      })
  }
  vanlynkSignIn() {
    this.router.navigate(['registration/vanlynkReg2'])
  }

  leadgenSignIn() {
    this.router.navigate(['registration/leadReg2'])
  }
}
