import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../utilities/beyond';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http';
import { } from 'googlemaps';
import { GeocodingService } from '../../../utilities/geocode.service';
import { icon, latLng, Map, marker, point, polyline, tileLayer } from 'leaflet';
import * as L from "leaflet";
import { Global } from '../../../global/global';
@Component({
  providers: [Beyond, GeocodingService],
  selector: 'app-vanlynk-registration2',
  templateUrl: './vanlynk-registration2.component.html',
  styleUrls: ['./vanlynk-registration2.component.scss']
})
export class VanlynkRegistration2Component implements OnInit {
  public dateTimeData = [];
  constructor(
    public beyond: Beyond,
    public router: Router,
    public http: Http,
    public activatedRoute: ActivatedRoute
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.getMasterDateTime();
    this.router.events.subscribe(x => {
      if (x instanceof NavigationEnd) {
        window.scrollTo(0, 0);
      }
    });
  }


  getMasterDateTime() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/firstmasterDateTime', options)
      .subscribe(data => {
        var response = data.json();
        this.dateTimeData = response.response;
        console.log("datetime", JSON.stringify(this.dateTimeData))
      }, error => {
        // this.toastr.warning('Unable to connect to server');
      })
  }

  status(data) {
    if (data.status == "CLOSED") {
      data.fromTime = "";
      data.toTime = "";
    } else {
      data.fromTime = "09:00 AM";
      data.toTime = "06:00 PM";
    }
  }

}

