import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { VanlynkRegistration1Component } from './vanlynk-registration1/vanlynk-registration1.component';
import { RegistrationLayoutComponent } from './registration-layout/registration-layout.component';
import { LeadRegistration1Component } from './lead-registration1/lead-registration1.component';
import { LeadRegistration2Component } from './lead-registration2/lead-registration2.component';
import { VanlynkRegistration2Component } from './vanlynk-registration2/vanlynk-registration2.component';
import { Registration1Component } from './registration1/registration1.component';
import { RegistrationConfirmationComponent } from './registration-confirmation/registration-confirmation.component';

const routes: Routes = [{
  path: '', component: RegistrationLayoutComponent,
  children: [
    {
      path: 'vanlynkReg1',
      component: VanlynkRegistration1Component
    },
    {
      path: 'vanlynkReg2',
      component: VanlynkRegistration2Component
    }, {
      path: 'leadReg1',
      component: LeadRegistration1Component
    }, {
      path: 'leadReg2',
      component: LeadRegistration2Component
    },
    {
      path: 'Reg1',
      component: Registration1Component
    },
    {
      path: 'regConfirm',
      component: RegistrationConfirmationComponent
    },

  ]
}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class RegistrationRoutingModule { }
